export const aboutModule = {
  state: () => ({
    aboutArray: [
      // заключаем между [] выделенный кусок текста
      { id: 1, icon: 'icon.png', content: "[Приезд за 15 минут] или в удобное для Вас время" },
      { id: 2, icon: 'icon.png', content: "Применяем [новейшие и современные технологии]" },
      { id: 3, icon: 'icon.png', content: "Дигнистика [0 рублей]" },
      { id: 4, icon: 'icon.png', content: "Дигнистика [0 рублей]" },
      { id: 5, icon: 'icon.png', content: "Выдаем [официальную гарантию] производителя"},
      { id: 5, icon: 'icon.png', content: "Выдаем [официальную гарантию] производителя"},
      { id: 5, icon: 'icon.png', content: "Выдаем [официальную гарантию] производителя"},
    ],
  }),
  getters: {
    
  },
  mutations: {
    aboutString(state) {
      state.aboutArray.forEach(benefitsItem => {
        const regex = /(?<=\[)(.*?)(?=\])/;
        const str = benefitsItem.content;
        const mark = str.match(regex)[0];
        const resultString = str.replace(mark, '');
        const resultArr = resultString.split('[]')

        benefitsItem.structure = {
          firstText: resultArr[0],
          mark: mark,
          lastText: resultArr[1],
        }
      });
    }
  },
  actions: {

  },
  namespaced: true
}