<template>
  <header class="header">
    <div class="max-container">
      <div class="header__inner">
        <div class="burger-content">
          <div class="logo">
            <p>Грузовик</p>
          </div>

          <nav class="header__nav">
            <a href="#" class="active header__link">Услуги и цены</a>
            <a href="#" class="header__link">Главная</a>
            <a href="#" class="header__link">О компании</a>
            <a href="#" class="header__link">Контакты</a>
            <a href="#" class="header__link">Разное</a>
          </nav>
        </div>

        <div class="header__addres">
          <p class="adds">Москва и область</p>
          <p class="number">8 (495) 120-06-93</p>
        </div>
        <button class="header__burger">
          <span></span>
          <span></span>
          <span></span>
        </button>
      </div>
    </div>
    <!-- /container -->
  </header>
  <!-- /header -->
</template>

<script>
export default {
  mounted() {
    window.addEventListener("scroll", () => {
      let scrolling = window.pageYOffset;
      //header
      const header = document.querySelector(".header");
      if (scrolling > 100) {
        header.classList.add("_fixed");
      } else {
        header.classList.remove("_fixed");
      }
    });

    class burger {
      body;
      burger;
      burgerContent;
      burgerClose;
      burgerLinks;

      constructor(obj) {
        this.body = document.body;
        this.burger = document.querySelector(obj.burger);
        this.burgerContent = document.querySelector(obj.burgerContent);
        this.burgerClose = document.querySelector(obj.burgerClose);
        this.burgerLinks = document.querySelectorAll(obj.burgerLink);
      }
      burgerInit() {
        if (this.burgerLinks.length > 0) {
          this.burgerLinks.forEach((link) => {
            link.addEventListener("click", () => {
              if (this.burger.classList.contains("active")) {
                this.body.classList.remove("_look");
                this.burgerContent.classList.remove("bg-active");
                this.burger.classList.remove("active");
              }
            });
          });
        }
        if (this.burger) {
          this.burger.addEventListener("click", () => {
            this.burger.classList.toggle("active");
            this.burgerContent.classList.toggle("bg-active");
            this.body.classList.toggle("_look");
          });
        }
        if (this.burgerClose && this.burgerClose !== this.burger) {
          this.burger.classList.remove("active");
          this.burgerClose.classList.remove("bg-active");
          this.body.classList.remove("_look");
        } else if (this.burgerClose === burger) {
          console.error(
            `элементы '${obj.burger}' и '${obj.burgerClose}' являются одним элементом`
          );
        }
      }
    }

    const bur = new burger({
      burger: ".header__burger",
      burgerContent: ".header",
      burgerLink: ".header__link",
    });
    bur.burgerInit();
  },
};
</script>

<style lang="scss">
.header {
  padding: 28px 0;
  background: #fff;
  @include adaptiv-value("padding-top", 28, 20);
  @include adaptiv-value("padding-bottom", 28, 20);

  &._fixed {
    position: fixed;
    width: 100%;
    z-index: 999999999999;

    box-shadow: 0 0 30px #333;

    @media (max-width: $md3 +px) {
      .adds {
        display: none;
      }
    }
  }

  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 20px;
    position: relative;
  }

  &__burger {
    width: 36px;
    height: 22px;
    position: relative;
    background: none;
    z-index: 999999;

    display: none;
    flex-direction: column;
    justify-content: space-between;
    cursor: auto;
    transition: 0.2s;

    span {
      transition: 0.2s;
      height: 4px;
      width: 100%;
      background: $textColor;
      border-radius: 10px;
    }

    @media (max-width: $md3 +px) {
      display: flex;
    }

    &.active {
      position: relative;

      span:nth-child(2) {
        position: absolute;
        top: 50%;
        opacity: 0;
      }

      span:nth-child(1) {
        position: absolute;
        transform: rotate(45deg);
        top: 50%;
      }

      span:nth-child(3) {
        position: absolute;
        top: 50%;
        transform: rotate(-45deg);
      }
    }
  }

  &__nav {
    @media (min-width: $md3 +px) {
      flex-shrink: 0;
      display: flex;
      gap: 76px;
      @include adaptiv-value("gap", 76, 10);
      align-items: center;
      flex-wrap: wrap;
    }
  }

  .burger-content {
    @media (min-width: $md3 +px) {
      display: flex;
      justify-content: space-around;
      gap: 20px;
      align-items: center;
      flex-grow: 1;
    }
  }

  &__link {
    transition: color 0.1s;
    color: $textColor;
    font-size: 16px;
    font-weight: 500;
  }

  &__link:hover,
  &__link:active,
  &__link.active {
    color: $blue;
  }

  &__addres {
    @media (min-width: $md3 +px) {
      display: inline-flex;
      align-items: center;
      column-gap: 59px;
      @include adaptiv-value("column-gap", 58, 0);
      flex-wrap: wrap;
      justify-content: flex-end;
      flex-shrink: 500;
    }

    .gap {
      width: 59px;
    }
  }

  @media (max-width: $md3 +px) {
    .burger-content {
      position: fixed;
      transition: top 0.4s ease;
      top: -150%;
      left: 0;
      width: 100%;
      height: 100%;
      background: $gray;
      z-index: 999;
      display: flex;
      padding-top: 8px;
      flex-direction: column;

      .logo {
        position: relative;
        display: inline-block;
        margin: 0 auto;
        font-size: 30px;
        padding: 10px;
        margin-bottom: 15%;
      }

      .header__nav {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 20px;
      }

      .header__link {
        font-size: 25px;
        padding: 10px;
      }
    }

    .adds {
      position: absolute;
      bottom: -50px;
      right: 0;
      color: #aaaaaa;
      font-weight: 300;
    }
  }
}

header.bg-active {
  @media (max-width: $md3 +px) {
    .burger-content {
      top: 0;
      overflow: auto;
    }
  }
}
</style>