<template>
  <section class="map section">
    <div class="max-container">
      <div class="map__inner">
        <div class="map-blok">
          <div class="map-wrapper">
            <button
              class="map-button"
              :data-map-btn="mapItem.id"
              v-for="mapItem in mapArray"
              :key="mapItem.id"
              :style="`left: ${mapItem.pos.x}px; top: ${mapItem.pos.y}px;`"
            ></button>
          </div>
          <div class="map-list">
            <div 
              class="map-item" 
              :data-map-item="mapItem.id"

              v-for="mapItem in mapArray"
              :key="mapItem.id">
              <div class="map-item__wrap">
                <div class="map-item__close">
                  <span></span>
                  <span></span>
                </div>
                <div class="rating">
                  <v-rating :value="mapItem.rating" />
                </div>
                <div class="map-item__name">{{mapItem.name}}</div>
                <div class="map-item__level">
                  <span class="mark">Опыт работы:</span> {{mapItem.exp}}
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="map__content">
          <h2 class="tit-h2">
            Работаем во всех <br />
            районах Москвы и <br />
            бижайшего Подмосковья
          </h2>
          <p>
            <span class="mark">График работы:</span> <br />
            Всегда
          </p>
        </div>
      </div>
    </div>
    <!-- /container -->
  </section>
</template>

<script>
import vRating from './UI/v-rating.vue';
export default {
  components: { vRating },
  props: {
    mapArray: {
      type: Array,
      required: true,
    },
  },
  mounted() {
    const mapBtns = Array.from(document.querySelectorAll(".map-button"));

    mapBtns.forEach((btn) => {
      const num = btn.getAttribute("data-map-btn");
      const currentItem = document.querySelector(
        `.map-item[data-map-item="${num}"]`
      );
      if (currentItem) {
        const currentItemClose = document.querySelector(
          `.map-item[data-map-item="${num}"] .map-item__close`
        );

        btn.addEventListener("click", (e) => {
          if (currentItem.classList.contains("active")) {
            currentItem.classList.remove("active");
            currentItem.style.top = `${0}px`;
            currentItem.style.left = `${0}px`;
          } else {
            currentItem.classList.add("active");

            if (e.target.offsetTop < currentItem.clientHeight) {
              currentItem.style.left = `${e.target.offsetLeft + 25}px`;
              currentItem.style.top = `${e.target.offsetTop + 25}px`;
              console.log(currentItem.clientHeight);
            } else {
              currentItem.style.left = `${e.target.offsetLeft + 25}px`;
              currentItem.style.top = `${
                e.target.offsetTop - currentItem.clientHeight
              }px`;
            }
          }
        });
        currentItemClose.addEventListener("click", () => {
          currentItem.classList.remove("active");
          currentItem.style.top = `${0}px`;
          currentItem.style.left = `${0}px`;
        });
      }
    });
  },
};
</script>

<style lang="scss">
.map-blok {
  position: relative;
  overflow-x: auto;
  overflow-y: hidden;
  min-width: 50%;

  .map-wrapper {
    min-height: 458px;
    min-width: 948px;
    background: url(../assets/images/map.jpg) no-repeat center;
    position: relative;
  }
}

.map-button {
  position: absolute;
  width: 28px;
  height: 28px;
  border: solid 6px #fff;
  border-radius: 50%;
  background: $blue;
  box-shadow: 0px 4px 34px #5C7AEA;
  transition: 0.1s;
}

.map-item {
  &.active {
    visibility: visible;
    pointer-events: auto;
  }
  visibility: hidden;
  max-width: 190px;
  position: absolute;
  padding: 22px;

  background: #ffffff;
  box-shadow: 0px 4px 13px rgba(0, 0, 0, 0.25);
  border-radius: 10px;

  &__wrap {
    position: relative;
  }
  &__close {
    cursor: pointer;
    position: absolute;
    right: -10px;
    top: -10px;
    width: 30px;
    height: 30px;

    span {
      position: relative;
      display: block;
      width: 100%;
      height: 2px;
      background: #707070;
      border-radius: 10px;
    }
    span:first-child {
      transform: rotate(-45deg);
      top: 15px;
    }
    span:last-child {
      transform: rotate(45deg);
      top: 14px;
    }
  }
  &__name {
    margin-bottom: 20px;
    font-size: 16px;
  }

  &__level {
    font-size: 11px;
  }
  .rating {
    margin-bottom: 15px;
  }
  .mark {
  }
}

.map {
  &-button:active,
  &-button:hover,
  &-button.active {
    background: $black;
  }

  &__inner {
    min-height: 458px;
    display: flex;

    @media (max-width: 1000px) {
      flex-direction: column-reverse;
    }
  }
  font-size: 22px;

  &__content {
    padding: 95px 0 94px 94px;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .mark {
    color: #b9b9b9;
  }
  p {
    line-height: 34px;
  }
}
</style>