export const benefitsModule = {
  state: () => ({
    benefitsArray: [
      // заключаем между [] выделенный синим кусок текста
      { id: 1, content: "Доставка [от 1200 рублей]" },
      { id: 2, content: "Доставка [до 120 рублей]" },
      { id: 3, content: "Проверка [50 000 рублей]" },
      { id: 4, content: "Доставка [от 1200 рублей]" },
      {id: 5,content: "Выдаем [официальную гарантию] производителя"},
    ],
  }),
  getters: {
    
  },
  mutations: {
    benefitsString(state) {
      state.benefitsArray.forEach(benefitsItem => {
        const regex = /(?<=\[)(.*?)(?=\])/;
        const str = benefitsItem.content;
        const mark = str.match(regex)[0];
        const resultString = str.replace(mark, '');
        const resultArr = resultString.split('[]')

        benefitsItem.structure = {
          firstText: resultArr[0],
          mark: mark,
          lastText: resultArr[1],
        }
      });
    }
  },
  actions: {
    
  },
  namespaced: true
}