<template>
  <section class="support section">
    <div class="max-container">
      <section-title class="tit-h2">Заголовок слайдера</section-title>

      <div class="support-slider-wrapper">
        <button class="slider-btn support-button-next support-button">→</button>
        <button class="slider-btn support-button-prev support-button">←</button>
        <div class="support-slider swiper">
          <div class="swiper-wrapper">
            <div class="swiper-slide empty-slide"></div>

            <div class="swiper-slide" v-for="supportItem in supportArray" :key="supportItem.id">
              <div class="support-item">
                <h3 class="support-item__title">
                  {{supportItem.title}}
                </h3>
                <div class="support-item__name">{{supportItem.name}}</div>

                <p class="support-item__text">
                  {{supportItem.text}}
                </p>
                <div class="support-item__info">
                  <div class="support-item__ava">
                    <img :src="require(`@/assets/images/${supportItem.ava}`)" alt="" />
                  </div>
                  <div class="support-item__name">
                    {{supportItem.expert}}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- /container -->
  </section>
</template>

<script>
export default {
  props: {
    supportArray: {
      type: Array,
      required: true,
    }
  }
};
</script>

