.support-slider-wrapper {
  position: relative;

  @media (max-width: 1500px) {
    .empty-slide {
      display: none;
    }
  }
}

.support-button-next {
  right: 27%;
}

.support-button {
  top: 50%;
  transform: translateY(-50%);
}

.support-button-prev {
  left: 34%;
}
