<template>
  <div class="rating">
    <div class="rating__block rating-stars">
      <icon-star class="rating__star" />
      <icon-star class="rating__star" />
      <icon-star class="rating__star" />
      <icon-star class="rating__star" />
      <icon-star class="rating__star" />

      <div class="rating__after rating-stars" ref="ratingAfter">
        <icon-star class="rating__star" />
        <icon-star class="rating__star" />
        <icon-star class="rating__star" />
        <icon-star class="rating__star" />
        <icon-star class="rating__star" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "v-rating",
  props: {
    value: {
      type: Number,
      required: false,
      default: 5,
    },
  },
  mounted() {
    if (this.value <= 5 && this.value >= 0) {
      this.$refs.ratingAfter.style.width = `${this.value * 20}%`;
    } else {
      throw new Error(
        `v-rating :value=${this.value} more than 5 or less than 0 `
      );
    }
  },
};
</script>

<style scoped lang="scss">
.rating-stars {
  display: flex;
  flex-wrap: nowrap;
}
.rating__star {
  width: 15px;
}
.rating {
  display: inline-block;
  &__block {
    position: relative;
    width: 100%;
    .rating__star {
      fill: #c4c4c4;
    }
  }

  &__after {
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;

    .rating__star {
      min-width: 15px;
      fill: #ffac0c;
    }
  }
}
</style>
